<template>
    <a-modal
            width="60%"
            v-model="visible"
            :footer="null"
            :destroyOnClose="true"
            @afterClose="handleClose"
            @cancel="handleClose"
    >
        <div slot="title">
            <a-row>
                <a-col :span="1">
                    <img :src="require('@/assets/pc/images/dhico.png')"
                         style="z-index: 9999;height: 18px">
                </a-col>
                <a-col :span="23">
                    {{title}}
                </a-col>
            </a-row>
        </div>
        <a-spin :spinning="spinning">
            <a-form @submit="handleSubmit"
                    :autoFormCreate="(form)=>{this.form = form}">
                <a-form-item
                        label='人员'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 15 }"
                        fieldDecoratorId="user_id"
                >
                    <SelectUser/>
                </a-form-item>
                <a-form-item
                        label='单位名称'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 15 }"
                        fieldDecoratorId="department_name"
                >
                    <a-input/>
                </a-form-item>
                <a-form-item
                        label='入职日期'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 15 }"
                        fieldDecoratorId="started_at"
                >
                    <DateTime/>
                </a-form-item>
                <a-form-item
                        label='离职日期'
                        :labelCol="{ span: 5 }"
                        :wrapperCol="{ span: 15 }"
                        fieldDecoratorId="ended_at"
                >
                    <DateTime/>
                </a-form-item>
                <a-form-item
                        :wrapperCol="{ span: 12, offset: 5 }"
                >
                    <a-button type='primary' htmlType='submit'>
                        提交
                    </a-button>
                </a-form-item>
            </a-form>
        </a-spin>

    </a-modal>
</template>

<script>
    import SelectUser from '@/components/select/SelectUser'
    import DateTime from '@/components/select/DateTime'
    import {
        getUserInfoPartyRelation,
        createUserInfoPartyRelation,
        updateUserInfoPartyRelation
    } from '@/api/userInfoPartyRelation'

    export default {
        components: {SelectUser, DateTime},
        props: {
            id: {required: true, type: Number},
            visible: {required: true, type: Boolean},
        },
        model: {
            prop: 'visible',
            event: 'change'
        },
        data() {
            return {
                spinning: true,
                form: {},
            }
        },
        computed: {
            title() {
                return this.id ? '编辑经历' : '新增经历';
            }
        },
        methods: {
            fetch() {
                this.spinning = true;
                if (this.id === 0) {
                    this.spinning = false;
                    return;
                }

                getUserInfoPartyRelation(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            user_id: res.data.user_id,
                            department_name: res.data.department_name,
                            started_at: res.data.started_at,
                            ended_at: res.data.ended_at,
                        })
                        this.spinning = false;
                    })
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();

                if (this.id) {
                    request = updateUserInfoPartyRelation(this.id, data)
                } else {
                    request = createUserInfoPartyRelation(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                    this.$emit('afterSubmit');
                })
            },
            handleClose() {
                this.$emit('change', false);
            },
        },
        watch: {
            visible(newVal, oldVal) {
                if (newVal) {
                    this.fetch();
                } else {
                    this.title = '数据加载中。。';
                    this.data = [];
                }
            }
        }
    }
</script>

