<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-spin :spinning="!isReady">
            <BaseInfo
                    @init="init"
                    :isEdit="isEdit"
                    :info="baseInfo"
                    v-if="isReady"/>
            <div class="columns bac_white mt-15" style="min-height: 200px">
                <div style="width: 100%; padding: 20px;" v-if="isReady">
                    <a-button-group>
                        <a-button
                                type="primary"
                                :disabled="this.partyInfoShow==='relation'"
                                @click="()=>{this.partyInfoShow='relation'}">党员关系
                        </a-button>
                        <a-button
                                type="primary"
                                :disabled="this.partyInfoShow==='experience'"
                                @click="()=>{this.partyInfoShow='experience'}">党员发展经历
                        </a-button>
                    </a-button-group>
                </div>
                <div class="clearfix"></div>

                <PartyExperience
                        :isEdit="isEdit"
                        :userId="userId"
                        v-if="isReady"
                        v-show="partyInfoShow==='experience'"/>
                <PartyRelation
                        :isEdit="isEdit"
                        :userId="userId"
                        v-if="isReady"
                        v-show="partyInfoShow==='relation'"/>
            </div>
        </a-spin>
    </div>
</template>

<script>
    import {getUserWithPartyInfo} from '@/api/user';
    import TopButtons from './components/TopButtons';
    import BaseInfo from './components/BaseInfo';
    import PartyExperience from './components/PartyExperienceInfo';
    import PartyRelation from './components/PartyRelationInfo';

    export default {
        name: "Index",
        components: {
            TopButtons, BaseInfo, PartyExperience, PartyRelation,
        },
        props: {
            isEdit: {
                required: false,
                type: Boolean,
                default() {
                    return false;
                }
            }
        },
        data() {
            return {
                isReady: false,
                partyInfoShow: null,
                relationInfo: [],
            }
        },
        computed: {
            userId() {
                if (this.baseInfo.hasOwnProperty('id')) {
                    return this.baseInfo.id;
                }
                return 0;
            }
        },
        methods: {
            init() {
                this.isReady = false;
                getUserWithPartyInfo()
                    .then(res => {
                        this.baseInfo = res.data;
                        this.partyInfoShow = 'relation';
                        this.isReady = true;
                    });
            }
        },
        mounted() {
            this.init();
        },
        watch: {
            $route(val) {
                this.init();
            }
        }
    }
</script>

