<template>
    <div>
        <div class="columns col-oneline bac_white">
            <div class="column col-2">
                <h1 style="color: #af1a17;">
                    <span class="fr">
                        <a href="#">
                            <img :src="require('@/assets/pc/images/grda_zdjt.png')"/>
                            展开
                        </a>
                    </span>
                    <b>个人信息</b>
                </h1>
                <div style="width: 160px; margin: 0 auto;">
                    <ul style="margin: 10px">
                        <li>
                            <div style="width: 140px;height: 196px">
                                <img
                                        v-if="!isEdit"
                                        :src="getUrl(thumbnail)"
                                        style="width: 140px;height: 196px"
                                        alt=""/>
                                <ChangeThumbnail @init="parentInit" v-else/>
                            </div>
                        </li>
                        <li class="dyxm_pic">{{info.name}}</li>
                    </ul>
                </div>
            </div>
            <div class="column col-10">
                <table width="100%" border="0" cellspacing="0" cellpadding="0">
                    <tbody>
                    <tr>
                        <td height="40" bgcolor="#f8f4e9">
                            　　姓名：{{info.name}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      disabled/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40">　　姓别：{{info.sex}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      @click="handleEdit('sex','姓别')"/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40" bgcolor="#f8f4e9">　　出生日期：{{info.user_borned_at_format}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      @click="handleEdit('user_borned_at','出生日期')"/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40">　　所在单位：{{info.department?info.department.name:''}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      disabled/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40" bgcolor="#f8f4e9">　　职务：{{info.duty}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      @click="handleEdit('duty','职务')"/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40">　　入党年份：{{info.party_experience?info.party_experience.zsdy_passed_at_format:''}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      disabled/>
                        </td>
                    </tr>
                    <tr>
                        <td height="40" bgcolor="#f8f4e9">　　联系电话：{{info.cellphone}}
                            <a-button class="edit-button" size="small" shape="circle" icon="edit"
                                      v-if="isEdit"
                                      @click="handleEdit('cellphone','联系电话')"/>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <EditModal v-if="modalVisible"
                   @init="parentInit"
                   :fieldLabel="editFieldLabel"
                   :field="editField"
                   :id="info.id"
                   :defaultValue="editDefaultValue"
                   :visible.sync="modalVisible"/>
    </div>
</template>

<script>
    import EditModal from './EditBaseFieldModal';
    import ChangeThumbnail from './components/ChangeThumbnail';
    import {getImageUrl} from "@/libs/tools";

    export default {
        name: "BaseInfo",
        components: {EditModal, ChangeThumbnail},
        props: {
            info: {
                required: true,
                type: Object,
            },
            isEdit: {
                required: true,
                type: Boolean,
            }
        },
        data() {
            return {
                editFieldLabel: '',
                editField: '',
                editDefaultValue: '',
                modalVisible: false,
            }
        },
        computed: {
            thumbnail() {
                if (this.info.more === null) {
                    return '';
                }

                if (!this.info.more.hasOwnProperty('thumbnail')) {
                    return '';
                }

                return this.info.more.thumbnail;
            }
        },
        methods: {
            getUrl(url) {
                return getImageUrl(url);
            },
            parentInit() {
                this.$emit('init');
            },
            handleEdit(field, fieldLabel = null) {
                switch (field) {
                    case 'user_borned_at':
                        this.editDefaultValue = moment(this.info[field] * 1000);
                        break;
                    default:
                        this.editDefaultValue = this.info[field];
                        break;
                }

                this.editFieldLabel = fieldLabel !== null ? fieldLabel : field;
                this.editField = field;
                this.modalVisible = true;
            },
        }
    }
</script>

<style scoped>
    .edit-button {
        float: right;
        margin-right: 20px;
    }
</style>
