<template>
    <div>
        <a-button type="primary" v-if="isEdit" style="margin-left:20px" @click="handleCreate">新增</a-button>
        <div class="column col-12">
            <ul class="layui-timeline">
                <li class="layui-timeline-item" v-for="row in info">
                    <i class="layui-icon layui-timeline-axis">
                        <img :src="require('@/assets/pc/images/sjx_ico01.png')" width="20" height="20" alt=""/>
                    </i>
                    <div class="layui-timeline-content layui-text">
                        <div class="layui-timeline-title">
                            {{row.started_at_format}} 至 {{row.ended_at_format}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{row.department_name}}
                            <a-popconfirm title="确认要删除吗?"
                                          @confirm="handleDelete(row.id)"
                                          okText="是"
                                          cancelText="否">
                                <a-button class="edit-button"
                                          size="small"
                                          shape="circle"
                                          icon="delete"
                                          v-if="isEdit"/>
                            </a-popconfirm>

                            <a-button style="margin-left: 20px"
                                      class="edit-button"
                                      size="small"
                                      shape="circle"
                                      icon="edit"
                                      v-if="isEdit"
                                      @click="handleEdit(row.id)"/>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <FormModal :id="modalId" v-model="modalVisible" @afterSubmit="init"/>
    </div>
</template>

<script>
    import FormModal from './components/RelationFormModal'
    import {getUserWithPartyInfo} from '@/api/user';
    import {deleteUserInfoPartyRelation} from '@/api/userInfoPartyRelation'

    export default {
        name: "PartyRelation",
        components: {
            FormModal
        },
        props: {
            userId: {
                required: true,
                type: Number,
            },
            isEdit: {
                required: true,
                type: Boolean,
            }
        },
        data() {
            return {
                modalVisible: false,
                modalId: 0,
                info: [],
            }
        },
        methods: {
            init() {
                getUserWithPartyInfo()
                    .then(res => {
                        this.info = res.data.party_relation ? res.data.party_relation : [];
                    });
            },
            handleCreate() {
                this.modalId = 0;
                this.modalVisible = true;
            },
            handleEdit(id) {
                this.modalId = id;
                this.modalVisible = true;
            },
            handleDelete(id) {
                deleteUserInfoPartyRelation(id)
                    .then(res => {
                        this.$message.success('删除成功');
                        this.init();
                    })
            }
        },
        mounted() {
            this.init();
        },
    }
</script>

<style scoped>
    .edit-button {
        float: right;
        margin-right: 20px;
    }
</style>
