<template>
    <div>
        <uploadThumbnail v-model="url"/>
    </div>
</template>

<script>
    import uploadThumbnail from '@/components/upload/UploadThumbnail'
    import {getImageUrl} from "@/libs/tools";
    import {updateUserOneField} from '@/api/user';

    export default {
        components: {uploadThumbnail},
        name: "ChangeThumbnail",
        data() {
            return {
                url: null,
            }
        },
        watch: {
            url(val) {
                if (val) {
                    updateUserOneField(this.$store.state.common.user_id, {more_thumbnail: val})
                        .then(res => {
                            this.$message.success('修改成功');
                            this.$emit('init');
                        });
                }
            }
        }
    }
</script>

