<template>
    <a-modal
            width="60%"
            v-model="visible"
            title="修改个人信息"
            :footer="null"
            :destroyOnClose="true"
            :afterClose="handleClose"
    >
        <a-spin :spinning="spinning">
            <a-form @submit="handleSubmit"
                    :autoFormCreate="(form)=>{this.form = form}">
                <a-form-item
                        v-if="field==='sex'"
                        :label='fieldLabel'
                        :labelCol="{ span: 3 }"
                        :wrapperCol="{ span: 15 }"
                        :fieldDecoratorOptions="{valuePropName:'checked'}"
                        :fieldDecoratorId="field"
                >
                    <a-radio-group buttonStyle="solid" :defaultValue="defaultValue">
                        <a-radio-button :value="'男'" checked>男</a-radio-button>
                        <a-radio-button :value="'女'">女</a-radio-button>
                    </a-radio-group>
                </a-form-item>
                <a-form-item
                        v-else-if="field==='user_borned_at'"
                        :label='fieldLabel'
                        :labelCol="{ span: 3 }"
                        :wrapperCol="{ span: 15 }"
                        :fieldDecoratorId="field"
                >
                    <a-date-picker showTime/>
                </a-form-item>
                <a-form-item
                        v-else
                        :label='fieldLabel'
                        :labelCol="{ span: 3 }"
                        :wrapperCol="{ span: 15 }"
                        :fieldDecoratorId="field"
                >
                    <a-input/>
                </a-form-item>

                <a-form-item
                        :wrapperCol="{ span: 12, offset: 5 }"
                >
                    <a-button type='primary' htmlType='submit'>
                        提交
                    </a-button>
                </a-form-item>
            </a-form>
        </a-spin>
    </a-modal>
</template>

<script>
    import {getUserWithPartyInfo, updateUserOneField} from '@/api/user';

    export default {
        name: "EditBaseFieldModal",
        props: {
            id: {required: true, type: Number},
            fieldLabel: {required: true, type: String},
            field: {required: true, type: String},
            visible: {required: true, type: Boolean},
            defaultValue: {required: true},
        },
        data() {
            return {
                spinning: true,
                form: {},
            }
        },
        methods: {
            parentInit() {
                this.$emit('init');
            },
            fetch() {
                this.spinning = true;
                return getUserWithPartyInfo();
            },
            handleClose() {
                this.$emit('update:visible', false)
            },
            handleSubmit(e) {
                e.preventDefault();

                updateUserOneField(this.id, this.form.getFieldsValue())
                    .then(res => {
                        this.$message.success('修改成功');
                        this.parentInit();
                    })
            }
        },
        mounted() {
            this.$nextTick()
                .then(res => {
                    return this.fetch()
                })
                .then(res => {
                    let data = {};
                    data[this.field] = this.defaultValue;
                    this.form.setFieldsValue(data);
                    this.spinning = false;
                })
        }
    }
</script>

